<template lang="pug">
    v-container(fluid)
        Snackbar(ref="snackbar")
        v-layout(wrap).grid-closet
            UserClosetContent(ref="closet" @upload="preUpload" @show="show").xs12.md8
            UserClosetUploader(ref="closetUploader")
            UserPreviewer(ref="previewer" title="").xs12.md4
</template>

<script>
    import Snackbar from '@/components/common/Snackbar.vue'
    import UserClosetContent from '@/components/user/closet/UserClosetContent.vue'
    import UserClosetUploader from '@/components/user/closet/UserClosetUploader.vue'
    import UserPreviewer from '@/components/user/UserPreviewer.vue'
    export default {
        name: 'UserCloset',
        components: {
            Snackbar, UserClosetContent, UserClosetUploader, UserPreviewer
        },
        data: () => ({

        }),
        mounted () {
            // this.$refs.previewer.init();
        },
        methods: {
            preUpload () {
                this.$refs.closetUploader.show({ title: '' });
            },
            show ({ texture, skin }) {
                this.$axios.get(`/api/textures/raw/${texture.id}`, {
                    responseType: 'arraybuffer'
                }).then(res => {
                    let data = 'data:image/png;base64,' + btoa(new Uint8Array(res.data).reduce((data, byte) => data + String.fromCharCode(byte), ''));
                    this.$refs.snackbar.show('info', ` ${texture.name} !`);
                    if (skin) this.$refs.previewer.setSkin(data);
                    else this.$refs.previewer.setCape(data);
                });
            },
        }
    }
</script>

<style scoped lang="stylus">
    .grid-closet
        > *
            &:first-child
                padding-right 16px
                @media (max-width 959px)
                    padding-right 0
                    margin-bottom 8px
            &:last-child
                padding-left 16px
                @media (max-width 959px)
                    padding-left 0
                    margin-top 8px
</style>
