<template lang="pug">
    v-toolbar(dense flat tabs color="white").elevation-1
        v-toolbar-title.subheading {{ title }}
        v-spacer
        v-btn(icon @click="$emit('search')")
            v-icon mdi-magnify
        v-btn(icon @click="$emit('upload')")
            v-icon mdi-plus
        template(#extension)
            v-tabs(v-model="value" @change="v => $emit('tab', v)" grow)
                v-tab(v-for="(tab, i) in tabs" :key="i")
                    v-icon {{ tab.icon }}
                    span.pl-2 {{ tab.title }}
</template>

<script>
    export default {
        name: 'UserClosetToolbar',
        props: {
            title: {
                type: String,
                required: true
            },
            tabs: {
                type: Array,
                default: () => null
            }
        },
        data: () => ({
            value: null
        }),
    }
</script>

