<template lang="pug">
    v-dialog(v-model="dialog" persistent width="500px")
        v-card
            v-card-title.subheading {{ dialogTitle }}
            v-card-text.py-0
                v-text-field(
                    label=""
                    prepend-icon="mdi-flag"
                    v-model="name"
                    :error="nameError"
                    :error-messages="nameErrorMsg"
                    @input="acceptName"
                    :counter="nameLength"
                    :disabled="disable"
                    clearable
                    required
                )
                v-radio-group(v-model="type" row)
                    v-layout(row)
                        v-radio(label="" value="skin" :disabled="disable")
                        v-radio(label="" value="cape" :disabled="disable")
                        v-select(
                            dense
                            label=""
                            prepend-icon="mdi-tshirt-crew"
                            :items="models"
                            :disabled="disable || type !== 'skin'"
                            v-model="model"
                            style="flex: 1"
                        )
                v-flex
                    FileUpload(
                        ref="fileupload"
                        accept="image/png"
                        :validators="dynamicValidators"
                        :disabled="disable"
                        @success="acceptFile"
                    )
                        template(#default="props")
                            img(:src="props.file.url")
            v-card-actions
                v-checkbox(
                    dense
                    label=""
                    color="green darken-1"
                    v-model="isPublic"
                    :disabled="disable"
                ).v-input-no-message.ma-2
                v-spacer
                v-btn(flat color="red darken-1" @click="dialog = false" :disabled="disable") 
                v-btn(flat color="green darken-1" @click="submit" :disabled="disable") 
</template>

<script>
    import FileUpload from '@/components/common/FileUpload.vue'
    export default {
        name: 'UserClosetUploader',
        components: {
            FileUpload
        },
        data: () => ({
            dialog: false,
            dialogTitle: '',
            name: null,
            nameLength: 24,
            nameError: false,
            nameErrorMsg: null,
            type: 'skin',
            models: ['steve', 'alex'],
            model: 'steve',
            isPublic: false,
            disable: false
        }),
        computed: {
            dynamicValidators () {
                return this.type === 'skin' ?
                    [file => file.width === 64 && file.height === 64 || ' 64x64 .'] :
                    [file => file.width === 64 && file.height === 32 || ' 64x32 .']
            }
        },
        methods: {
            show ({ title }) {
                this.dialog = true;
                this.dialogTitle = title;
                this.name = null;
                this.nameErrorMsg = null;
                this.nameError = false;
                this.model = 'steve';
                this.$refs.fileupload.clear();
            },
            async submit (evt) {
                evt.preventDefault();
                this.acceptName();
                if (!this.$refs.fileupload.validate('.'))
                    return;
                this.disable = true;
                this.$store.dispatch('addCloset', {
                    name: this.name,
                    type: this.type,
                    model: this.model,
                    isPublic: this.isPublic,
                    file: this.$refs.fileupload.getFile().url
                }).then(res => {
                    this.$parent.$refs.snackbar.show('success', '!', () => {
                        this.disable = false;
                        this.dialog = false;
                    });
                }).catch(err => {
                    let msg = err.message;
                    if (err.response && err.response.data.msg)
                        msg = err.response.data.msg;
                    this.$parent.$refs.snackbar.show('error', ': ' + msg);
                    this.disable = false;
                });
            },
            acceptName (name) {
                if (!this.name && name) {
                    this.name = name;
                    this.nameError = false;
                    this.nameErrorMsg = null;
                }
                if (!this.name) {
                    this.nameErrorMsg = '';
                    this.nameError = true;
                } else if (this.name.length > this.nameLength) {
                    this.nameErrorMsg = "";
                    this.nameError = true;
                } else {
                    this.nameError = false;
                    this.nameErrorMsg = null;
                }
            },
            acceptFile (file) {
                this.acceptName(file.name.substring(0, file.name.lastIndexOf('.')));
            }
        },
        mounted () {
        }
    }
</script>

<style lang="stylus">
    .upload-drop
        height 80px
        border 1px dashed #aaa
        border-radius 2px
        display flex
        justify-content center
        align-items center
    .upload-drop.draggable
        border 2px dashed #aaa
</style>
