<template>
    <v-tab-item>
        <v-card flat>
            <v-card-text>
                <v-layout wrap class="grid-skin">
                    <v-flex v-for="skin in skins" xs12 md4 :key="skin.id">
                        <v-card hover>
                            <v-card-text @click.stop="$emit('show', { texture: skin, skin: true })">
                                <v-img :src="`/api/textures/skin2d/${skin.id}?slim=${skin.model === 'alex'}`"></v-img>
                            </v-card-text>
                            <v-divider></v-divider>
                            <v-card-actions class="pa-1 px-2">
                                        <span style="font-size: 1.2rem;">
                                            {{ skin.name }}
                                            <span style="font-size: 0.75rem; color: grey;">
                                                ({{ skin.model || 'steve' }})
                                            </span>
                                        </span>
                                <v-spacer></v-spacer>
                                <v-menu bottom origin="top left" transition="scale-transition">
                                    <template #activator="{ on }">
                                        <v-btn icon v-on="on">
                                            <v-icon style="font-size: 1.2rem;">
                                                mdi-settings
                                            </v-icon>
                                        </v-btn>
                                    </template>
                                    <v-list>
                                        <v-list-tile @click="">
                                            <v-list-tile-title></v-list-tile-title>
                                        </v-list-tile>
                                        <v-list-tile @click="">
                                            <v-list-tile-title></v-list-tile-title>
                                        </v-list-tile>
                                        <v-list-tile @click="setAvatar(skin)">
                                            <v-list-tile-title></v-list-tile-title>
                                        </v-list-tile>
                                    </v-list>
                                </v-menu>
                            </v-card-actions>
                        </v-card>
                    </v-flex>
                </v-layout>
            </v-card-text>
            <v-divider></v-divider>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-pagination
                        v-model="page"
                        :length="totalPages"
                        :total-visible="6"
                ></v-pagination>
            </v-card-actions>
        </v-card>
    </v-tab-item>
</template>

<script>
    export default {
        name: 'UserClosetTabSkin',
        data: () => ({
            skins: [],
            page: 1,
            perPage: 3,
            totalPages: 0
        }),
        watch: {
            page (newValue, oldValue) {
                if (newValue !== oldValue)
                    this.fetchSkins();
            }
        },
        mounted () {
            this.fetchSkins();
        },
        methods: {
            async fetchSkins () {
                this.$store.dispatch('getCloset', {
                    category: 'skin',
                    page: this.page,
                    perPage: this.perPage
                }).then(res => {
                    this.totalPages = res.data.data.total_pages;
                    this.skins = res.data.data.items;
                }).catch(err => {
                    console.log(err);
                })
            }
        },
    }
</script>

<style scoped lang="stylus">
    .grid-skin
        > .flex
            padding-bottom 1rem
            @media (min-width 959px)
                &:nth-child(3n+1)
                    padding-right 0.6rem
                &:nth-child(3n+0)
                    padding-left 0.6rem
                &:nth-child(3n+2)
                    padding 0 0.3rem
</style>
