<template lang="pug">
    v-flex
        UserClosetToolbar(
            :title="title"
            :tabs="tabs"
            @tab="v => { tab = v }"
            @search=""
            @upload="$emit('upload')"
        )
        v-tabs-items(:value="tab").white.elevation-1
            UserClosetTabSkin
            UserClosetTabCape(lazy)
</template>

<script>
    import UserClosetToolbar from './UserClosetToolbar.vue'
    import UserClosetTabSkin from './UserClosetTabSkin.vue'
    import UserClosetTabCape from './UserClosetTabCape.vue'
    export default {
        name: 'UserClosetContent',
        components: {
            UserClosetToolbar, UserClosetTabSkin, UserClosetTabCape
        },
        data: () => ({
            title: '',
            tab: null,
            tabs: [
                { icon: 'mdi-tshirt-crew', title: '' },
                { icon: 'mdi-sack', title: '' }
            ],
        }),
        methods: {
            setAvatar (skin) {
                this.$store.dispatch('setAvatar', { avatarId: skin.id }).then();
            }
        }
    }
</script>

<style scoped lang="stylus">
</style>
