<template lang="pug">
    .fileupload-container
        .fileupload(ref="fileupload" :class="errorClass")
            v-btn(v-if="file" icon @click.stop="clear" :disabled="disabled").fileupload-clear.ma-1
                v-icon mdi-close
            .fileupload-box(:style="style")
                .fileupload-ctx(ref="ctx" :style="visible")
                    input(ref="input" type="file" :accept="accept" :disabled="disabled")
                    span {{ text }}
                slot(v-if="file" :file="file")
        .fileupload-error(v-if="error").pt-2.pb-0.red--text
            span {{ error }}
</template>

<script>
    export default {
        name: 'FileUpload',
        props: {
            text: {
                type: String,
                default: '...'
            },
            height: {
                type: Number,
                default: 80
            },
            accept: {
                type: String,
                default: null
            },
            validators: {
                type: Array,
                default: () => []
            },
            disabled: {
                type: Boolean,
                default: false
            }
        },
        data: () => ({
            file: null,
            error: null
        }),
        computed: {
            style () {
                return { 'min-height': this.height + 'px' };
            },
            visible () {
                return this.file ? { 'display': 'none' } : { };
            },
            errorClass () {
                return this.error ? 'fileupload-error' : null;
            },
            accepts () {
                return this.accept ? this.accept.split(',') : [ ];
            }
        },
        methods: {
            getFile () {
                return this.file;
            },
            toggleDrag (flag) {
                this.$refs.fileupload.classList.toggle('drag', flag);
            },
            dragEnter (evt) {
                evt.stopPropagation();
                evt.preventDefault();
                this.toggleDrag(true);
            },
            dragLeave (evt) {
                evt.stopPropagation();
                evt.preventDefault();
                this.toggleDrag(false);
            },
            dragOver (evt) {
                evt.stopPropagation();
                evt.preventDefault();
                if (this.disabled) {
                    evt.dataTransfer.dropEffect = 'none';
                    this.toggleDrag(false);
                } else {
                    this.toggleDrag(true);
                }
            },
            async compute (files) {
                return new Promise(resolve => {
                    let rawFile = files[0];
                    if (!rawFile)
                        return resolve({ error: '.' });
                    let file = {
                        name: rawFile.name,
                        size: rawFile.size,
                        type: rawFile.type,
                        raw: rawFile
                    };
                    if (!this.accept || this.accepts.indexOf(file.type) === -1)
                        return resolve({ error: '.' });
                    let reader = new FileReader();
                    reader.onload = (e) => {
                        file.url = e.target.result;
                        if (file.type.startsWith('image')) {
                            let img = new Image();
                            img.onload = (e) => {
                                file.width = e.target.width;
                                file.height = e.target.height;
                                let error = this.validateFile(file);
                                resolve({ file, error })
                            };
                            img.src = file.url;
                        } else {
                            file.width = file.height = -1;
                            let error = this.validateFile(file);
                            resolve({ file, error })
                        }
                    };
                    reader.readAsDataURL(rawFile);
                });
            },
            validateFile (file) {
                if (!this.validators)
                    return null;
                for (let i = 0; i < this.validators.length; i++) {
                    let validator = this.validators[i];
                    if (typeof validator === 'function') {
                        let result = validator(file);
                        if (result !== true)
                            return result;
                    }
                }
                return null;
            },
            validate (msg) {
                if (!this.file && msg)
                    this.error = msg;
                return this.file !== null;
            },
            drop (evt) {
                evt.stopPropagation();
                evt.preventDefault();
                if (this.disabled)
                    return;
                this.toggleDrag(false);
                this.ready(evt.dataTransfer.files);
            },
            change (evt) {
                if (this.disabled)
                    return;
                this.ready(evt.target.files);
                this.$refs.input.value = null;
            },
            ready (files) {
                this.compute(files).then(result => {
                    if (result.error) {
                        this.error = result.error;
                    } else {
                        this.error = null;
                        this.file = result.file;
                        this.unregisterListeners();
                        this.$emit('success', result.file);
                    }
                });
            },
            clear (evt) {
                if (evt)
                evt.preventDefault();
                this.file = null;
                this.error = null;
                this.$refs.input.value = null;
                this.registerListeners();
            },
            registerListeners() {
                this.$refs.fileupload.ondragenter = this.dragEnter;
                this.$refs.fileupload.ondragleave = this.dragLeave;
                this.$refs.fileupload.ondragover = this.dragOver;
                this.$refs.fileupload.ondrop = this.drop;
                this.$refs.input.onchange = this.change;
            },
            unregisterListeners() {
                this.$refs.fileupload.ondragenter = null;
                this.$refs.fileupload.ondragleave = null;
                this.$refs.fileupload.ondragover = null;
                this.$refs.fileupload.ondrop = null;
                this.$refs.input.onchange = null;
            },
        },
        mounted () {
            this.registerListeners();
        }
    }
</script>

<style lang="stylus">
    .fileupload
        border 1px dashed #aaa
        border-radius 4px
        position relative
        .fileupload-clear
            position absolute
            top 0
            right 0
        .fileupload-box
            display flex
            justify-content center
            align-items center
            padding 1rem
        .fileupload-ctx
            position relative
            span
                color #aaa
                font-size 1.2rem
            input
                min-height 0
                opacity 0
                position absolute
                width 100%
                height 100%
    .fileupload.drag
        border 2px dashed #aaa
        box-shadow 0 0 3px yellowgreen
    .fileupload.fileupload-error
        border 1px dashed red
        box-shadow 0 0 3px red
</style>
